<template>
    <div class="page-index">
        <van-nav-bar            
            left-arrow
            title="我的认证"
            @click-left="onClickLeft"
        >    
        </van-nav-bar>
        <div class="main">
            <div class="item" @click="showPopup">
                <p class="label label_1" >认证车型</p>
                <p class="value">{{data.car_1}}{{data.car_2}}{{data.car_3}}</p>
            </div>
            <van-divider class="line" />
            <div class="item">
                <p class="label">行驶证正面照片<span>（横向拍摄识别更精确）</span></p>
                
                <van-uploader    :after-read="afterRead" >
                    <div class="image-box">
                        <img :src="data.base64" v-if="data.base64"  >
                    </div>
                </van-uploader>
            </div>
            <div class="item">
                <ul>
                    <li>
                        <div class="flex align-items-center">
                            <span class="label flex-none">姓名</span><van-field v-model="data.realname" placeholder="姓名" right-icon="cross" />
                        </div>
                        <van-divider class="li-line" />
                    </li>
                    <li>
                        <div class="flex align-items-center">
                            <span class="label flex-none">车牌号码</span><van-field v-model="data.plate_number" placeholder="车牌号码" right-icon="cross" />
                        </div>
                        <van-divider class="li-line" />
                    </li>
                    <li>
                        <div class="flex align-items-center">
                            <span class="label flex-none">联系电话</span><van-field v-model="data.mobile" placeholder="联系电话" right-icon="cross" />
                        </div>
                        <van-divider class="li-line"  />
                    </li>                                        
                </ul>
            </div>
            <div class="btn" @click="checkForm">
                {{data.action == 'add' ? '添加':'重新认证'}}
            </div>
        </div>
        <!--车型一级选择-->
        <van-popup position="right" id="id" :style="{height:'100%',width:'80%'}" v-model="show">
            <div class="popup">
            <div class="popup_header" @click="showClick">
                <img class="popup_header_img" src="../../assets/images/left.png" alt />
                <span class="popup_header_span">选择品牌</span>
            </div>
            <div class="popup_content">
                <div class="popup_content_kuai" v-for="(item,index) in list" :key="index">
                <van-index-anchor ref="indexes" class="popup_content_title">{{index}}</van-index-anchor>
                <div
                    class="popup_content_text"
                    @click="twoClick(item1)"
                    v-for="(item1,key) in item"
                    :key="key"
                >
                    <img :src="item1.icon" class="popup_content_text_img" alt />
                    <span class="popup_content_text_span">{{item1.name}}</span>
                </div>
                </div>
            </div>
            </div>
        </van-popup>
        <!--问答er级选择-->
        <van-popup position="right" :style="{height:'100%',width:'80%'}" v-model="vehicleShow">
            <div class="popup">
            <div class="popup_header" @click="vehicleClick">
                <img class="popup_header_img" src="../../assets/images/left.png" alt />
                <span class="popup_header_span">选择车系</span>
            </div>
            <div class="popup_content">
                <div
                class="popup_content_vehicle"
                @click="vehicleItemClick(item.name,item.id)"
                v-for="(item,index) in vehicle"
                :key="index"
                >
                <img :src="item.icon" class="popup_content_vehicle_img" alt />
                <span class="popup_content_vehicle_span">{{item.name}}</span>
                </div>
            </div>
            </div>
        </van-popup>
    </div>
</template>

<script>
import { Toast } from 'vant';
export default {
    data(){
        return {
            show:false,
            vehicleShow:false,
            list:[],
            vehicle:[],
            data:{
                car_1:'',
                car_2:'',
                car_3:'',
                myfile:'',
                base64:'',
                action:this.$route.query.type,
                vid:'',
                plate_number:'',
                mobile:'',
                realname:'',
                token:this.$store.state.userData
            },
            carc: {
                token: this.$store.state.userData,
                upid: 0,
                type: ""
            },
        }
    },
    created(){

    },
    mounted(){
        this.api.getCars(this.carc).then(res => {
        if (res.err_code == 0) {
            this.list = res.data;
        }
        });
    },
    methods:{
        //隐藏车系
        showClick() {
            this.show = false;
        },
        //选择车辆
        showPopup() {
            this.show = true;
        },
        //选择车型
        vehicleItemClick(name, id) {
            console.log(name);        
            this.data.car_2 = name;
            this.vehicleShow = false;
        },
        //选择车类型
        twoClick(item) {
            this.carc.upid = item.id;
            this.data.car_1 = item.name;
            this.carc.type = "";
            this.api.getCars(this.carc).then(res => {
                if (res.err_code == 0) {
                    this.show = false;
                    this.vehicleShow = true;
                    this.vehicle = res.data;
                }
            });
        },        
        //返回车中
        vehicleClick() {
            this.vehicleShow = false;
            this.show = true;
        
        },
        afterRead(file){
            this.data.base64 = file.content;
            this.data.myfile = file.file;
  
        },         
        onClickLeft(){
            this.$router.go(-1)
        },
        checkForm(){
            if(!this.data.car_1){
                return Toast('请选择车型');
            }
            if(!this.data.myfile){
                return Toast('请上传驾驶证照片');
            }
            if(!this.data.realname){
                return Toast('请输入姓名');
            }
            if(!this.data.plate_number){
                return Toast('请输入车牌号码');
            }
            if(!this.data.mobile){
                return Toast('请输入联系电话');
            }
            this.hadnleSubmit();
        },
        hadnleSubmit(){
            Toast.loading({
                message: '加载中...',
                forbidClick: true
            });
            let formData = new FormData();
            for(let k  in this.data ){
                if(k != 'base64' ){
                    formData.append(k,this.data[k])
                }
            }
            this.api.saveCarVerify(formData).then(res => {
                if(res.err_code == 0){
                    Toast.clear();
                    this.onClickLeft();
                }else {
                    Toast(res.err_msg);
                }
            })
            
        },
    }
}
</script>

